import React, { useState, useEffect, memo, useCallback } from "react";
import { useDispatch, useSelector } from "react-redux/es/exports";
import { useLocation, useSearchParams } from "react-router-dom";
import { fetchDataCarOrders } from "../../redux/dataThunk/fetchDataThunk";
import CarOrdersInput from "../CarOrdersInputs";
import PaginationCarOrders from "../Common/PaginationAdminPanel";
import OrderCardLine from "../OrderCardLine";
import "./index.scss";

const limitCarsPerPage = 3;

const CarOrders = () => {
  const dispatch = useDispatch();
  const [searchParams] = useSearchParams();
  const [currentPage, setCurrentPage] = useState(1);
  const { dataOrders, loadingOrders } = useSelector(({ getData }) => getData);
  const { totalCars } = dataOrders;
  const { data } = dataOrders;
  const location = useLocation();
  const paginate = (pageNumber) => setCurrentPage(pageNumber);

  const stringFilter = useCallback(
    () => ({
      CarsId: searchParams.get("CarsId") || "",
      periodOfTime: searchParams.get("additionalOptions.startDate_gte") || "",
      periodOfTimeTwo: searchParams.get("additionalOptions.startDate_lte") || '',
      cityAuto: searchParams.get("additionalOptions.cityAuto") || "",
      orderStatus: searchParams.get("additionalOptions.endDate_gte") || "",
      orderStatusTwo: searchParams.get("additionalOptions.endDate_lte") || "",
    }),
    [searchParams]
  );

  useEffect(() => {
    dispatch(
      fetchDataCarOrders({
        urlFilter: stringFilter(),
        page: currentPage,
        limit: limitCarsPerPage,
      })
    );
  }, [dispatch, location.search, currentPage, stringFilter]);

  return (
    <div>
      <CarOrdersInput />
      {!loadingOrders ? (
        <OrderCardLine dataCurrent={data || []} />
      ) : (
        <h3 className="carOrders__notFound"> </h3>
      )}
      <PaginationCarOrders
        carPerPage={limitCarsPerPage}
        totalCars={totalCars}
        paginate={paginate}
        setCurrentPage={setCurrentPage}
        currentPage={currentPage}
      />
    </div>
  );
};

export default memo(CarOrders);
