import React, { useEffect, useState } from "react";
import classNames from "classnames";
import { useSelector, useDispatch } from "react-redux";
import { useLocation } from "react-router-dom";
import {
  setImgForCar,
  setDescriptionCar,
} from "../../redux/cart/reducerCarCartSettings";
import {  fetchDataCarList  } from "../../redux/dataThunk/fetchDataThunk";
import {
  setLinkImgError,
  setDescriptionError,
} from "../../redux/cart/reducerValidateErrors";
import style from "./index.scss";


const cn = classNames.bind(style);

function randomLevel(min, max) {
  return Math.round(Math.random() * (max - min) + min);
}
const randomFuelLvl = randomLevel(20, 50);

const CarCardSetup = () => {
  const dispatch = useDispatch();

  const { imgCar, modelCarCart, typeCarCart, descriptionCar } = useSelector(
    ({ carSettings }) => carSettings
  );
  const { linkError, descriptionError } = useSelector(
    ({ validateErrors }) => validateErrors
  );

  const [linkDirty, setLinkDirty] = useState(false);
  const [descriptionDirty, setDescriptionDirty] = useState(false);

  const handlClickBlur = (e) => {
    switch (e.target.name) {
      case "link":
        setLinkDirty(true);
        break;
      case "description":
        setDescriptionDirty(true);
        break;
      default:
    }
  };

  const handlChangeImg = (value) => {
    const re =
      /(http(s)?:\/\/.)?(www\.)?[-a-zA-Z0-9@:%._+~#=]{2,256}\.[a-z]{2,6}\b([-a-zA-Z0-9@:%_+.~#?&//=]*)/g;
    if (!re.test(String(value).toLowerCase())) {
      dispatch(setLinkImgError(" "));
      dispatch(setImgForCar(value));
    } else {
      dispatch(setImgForCar(value));
      dispatch(setLinkImgError(""));
    }
  };

  const handlChangeDescription = (value) => {
    const re = /^[--a-zA-Z0-9\s?!,.']+$/;
    if (value.length < 10 || !re.test(value)) {
      dispatch(setDescriptionError("  "));
      dispatch(setDescriptionCar(value));
    } else {
      dispatch(setDescriptionCar(value));
      dispatch(setDescriptionError(""));
    }
  };

  const choiseFromArr = modelCarCart.split(",");
  const model = choiseFromArr[0];
  const name = choiseFromArr[1];

  const { dataCarList } = useSelector(({ getData }) => getData);
  const imgCarFromBack = dataCarList?.imgCar;
  const descriptionCarFromBack = dataCarList?.descriptionCar;
  
  const location = useLocation();
  useEffect(() => {
    dispatch(fetchDataCarList());
    setTimeout(() => {
      dispatch(setImgForCar(imgCarFromBack || imgCar));
      dispatch(setDescriptionCar(descriptionCarFromBack || descriptionCar));
      dispatch(setLinkImgError(""));
      dispatch(
        setDescriptionError(
          descriptionCarFromBack ? "" : "    "
        )
      );
    }, 0);
  }, [imgCarFromBack, descriptionCarFromBack, location]);

  return (
    <div className="arCardSetup">
      <div className="arCardSetup__carSetup">
        <img src={imgCar} alt="carImg" className="arCardSetup__img"></img>
        <span className="arCardSetup__carName">
          {model && name ? (
            <span>
              {model}, {name}
            </span>
          ) : (
            "    "
          )}
        </span>
        <span className="arCardSetup__carClass">{typeCarCart}</span>
        <form className="arCardSetup__formForInputFile ">
          <label htmlFor="inputFile" className="arCardSetup__label">
            <input
              className={cn("arCardSetup__input", {
                arCardSetup__input_error: linkDirty && linkError,
              })}
              id="inputFile"
              placeholder="  ..."
              value={imgCar}
              onChange={(e) => handlChangeImg(e.target.value)}
              name="link"
              onBlur={(e) => handlClickBlur(e)}
            ></input>
            {linkDirty && linkError && (
              <div className="arCardSetup__textError">{linkError}</div>
            )}
            <button
              type="button"
              className={cn("arCardSetup__button", {
                arCardSetup__button_error: linkDirty && linkError,
              })}
            >
              
            </button>
          </label>
        </form>
      </div>
      <div className="arCardSetup__progressSetup">
        <div className="arCardSetup__textContainer">
          <span className="arCardSetup__textCompleted"></span>
          <p className="arCardSetup__textPercentage">{randomFuelLvl}%</p>
        </div>
        <progress max="100" value={randomFuelLvl}></progress>
      </div>
      <div className="arCardSetup__description">
        <span className="arCardSetup__titleDescription"></span>
        <textarea
          className="arCardSetup__textArea"
          placeholder="  ..."
          value={descriptionCar}
          onChange={(e) => handlChangeDescription(e.target.value)}
          name="description"
          onBlur={(e) => handlClickBlur(e)}
        ></textarea>
        {descriptionDirty && descriptionError && (
          <div className="arCardSetup__textError">{descriptionError}</div>
        )}
      </div>
    </div>
  );
};

export default CarCardSetup;
